$primary-color:#1ed046;

/*=== Text color ===*/
a:hover, a:focus,
.primary-color,.testimonial-carousel .owl-nav .owl-next:hover, .testimonial-carousel .owl-nav .owl-prev:hover,.testimonial-carousel span.client-status,footer ul.footer-links li a:hover,[class*="copyright-"] .footer-social a:hover,[class*="copyright-"] p a,.header-transparent.navbar-sticky.main-nav .nav-item.active a.nav-link,.header-transparent.navbar-sticky .nav-item a.nav-link:hover,.main-nav .dropdown-menu a:hover,.card.team-card .card-body a:hover,.single-address .icon i.fa,.error-wrapper h1,.page-comingsoon .count-down .tab-val,.side-menu li a:hover,.side-menu li a:hover i.fa,.single-comment .comment .replay-link,.btn.btn-white:hover,ul.feature-list i.fa,.card.team-card .social-icons a.icon:hover,.page-comingsoon .form_subscribe i,
 {
	color: $primary-color;
}


.text-primary, a.text-primary:focus, a.text-primary:hover,.btn.btn-primary:hover{
	color: $primary-color!important;
}

/*=== Background color ===*/

.btn-primary, .btn-primary:focus, .btn-primary.active, .btn-primary:active,.btn-primary:hover,
.sep:before,.bg-image:before, .default-overlay:before,.main-nav .navbar-toggler,.info.service-info:hover ,.service-carousel .owl-dots .owl-dot:before,.bg-primary,.btn.btn-primary,.bg-primary,.radio-custom:checked + .radio-custom-label:before,.checkbox-custom:checked + .checkbox-custom-label:before,.btn-transparent:hover,ul.social-icons a:hover,ul.social-icons.gray-color a:hover {
	background-color: $primary-color;
}


.bg-primary{
	background-color: $primary-color!important;
}

::-moz-selection {
	background-color: $primary-color;

}
::selection {
	background-color: $primary-color;
}


/*=== Border color ===*/
input.form-control:focus,
textarea.form-control:focus,
input.form-control.active,
input.form-control:active,hr.hr_narrow,btn.btn-white:hover,.radio-custom:checked + .radio-custom-label:before,.checkbox-custom:checked + .checkbox-custom-label:before,.btn-transparent:hover{
	border-color: $primary-color;
}
.btn.btn-primary:active,.btn.btn-primary:focus,.page-comingsoon .count-down .tab-val {
	border-color: $primary-color!important;
}

/*=== Gradient Color & RGBA Color ===*/

.bg-image:after,.default-overlay::after {
	background: linear-gradient(to bottom, rgba($primary-color,0.8) 0%,rgba($primary-color,0.1) 42%,rgba($primary-color,0.1) 100%);
}

.hover-overlay.effect-transformY{
	background: rgba($primary-color, 0.85);
}
.info .icon .fill-color{
    fill: $primary-color;
}
.changed-color.logo-color {
    fill: $primary-color!important;
}